//Aysnc GET with website
const getTemp = async(webSite, code, apiKey) => {
    const webRes = await fetch(webSite + code + ',US' + '&APPID=' + apiKey)
    console.log(webRes);
    try {
        const data = await webRes.json();
        console.log(data);

        return data;
    } catch (error) {
        console.log('error', error);
    }
}